@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.heading {
    text-align: center;
}

h1 {
    letter-spacing: .5rem;
}

input[type='text'] {
    width: 30rem;
    height: 2rem;
    padding: .7rem;
    border-radius: .2rem;
    border: none;
}

input[type='text']:hover {
    box-shadow: .2rem .2rem rgba(128, 128, 128, 0.274);
}

.search {
    padding: .5rem;
    width: 6rem;
    margin: .6rem;
    border-radius: .2rem;
    border: none;
    background: linear-gradient(to left, rgba(235, 205, 33, 0.596), rgba(200, 218, 35, 0.733));
    cursor: pointer;
    letter-spacing: .1rem;
}

.search:hover {
    background: linear-gradient(rgba(235, 205, 33, 0.623), rgba(200, 218, 35, 0.986));
}

.search:focus,
.search:active {
    background: linear-gradient(rgb(235, 205, 33), rgba(200, 218, 35, 0.986));
}

.container {
    margin: 0 auto;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}

ul {
    list-style: none;
}

/* Scroll to top */
.scrollToTop{
    position: fixed;
    bottom: 0;
    right: 0;
    height: 3rem;
    width: 3rem;
    background-color: rgba(0, 0, 0, 0.726);
    text-align: center;
    border-radius: 1.5rem;
    cursor: pointer;
    z-index: 1;
    border: 1px solid yellow;
}

.arrowTop{
    color: rgb(255, 238, 0);
    font-size: 1.5rem;
    font-weight: 900;
    text-align: center;
}

.movie-card {
    align-items: center;
    margin: 2rem;
    width: 150px;
    cursor: pointer;
}

img {
    height: 200px;
    width: 150px;
    background-size: contain;
    border-radius: 1rem;
}

.detail-card {
    font-size: 1.2rem;
    font-weight: lighter;
    text-align: center;
}

.detail-card>p {
    text-transform: capitalize;
    font-size: 1rem;
}

.image-card:hover {
    -webkit-animation: sizeUp .5s forwards;
            animation: sizeUp .5s forwards;
}

@-webkit-keyframes sizeUp {
    to {
        transform: scale(1.2);
    }
}

@keyframes sizeUp {
    to {
        transform: scale(1.2);
    }
}

.year {
    font-size: .9rem;
}

.link_details {
    color: white;
    text-decoration: none;
}
.movie-Board {
    padding: 1.2rem;
    margin: 1rem;
    background: linear-gradient(to top, rgba(235, 205, 33, 0.185), rgba(77, 245, 209, 0.027));
    color: rgba(194, 239, 241, 0.829);
    display: grid;
    grid-template-columns: 40% 60%;
    border: .1rem solid rgb(156, 146, 146);
    border-radius: .5rem;
    box-shadow: .1rem .1rem rgb(156, 146, 146),
        .2rem .2rem rgb(8, 8, 7),
        .3rem .3rem rgb(156, 146, 146);
}

.loading {
    text-align: center;
    justify-content: center;
    align-items: center;
    color: rgba(255, 230, 0, 0.836);
}

.title {
    font-size: larger;
    font-weight: 800;
}

p {
    margin-bottom: 1rem;
}

.head-text {
    color: rgba(101, 220, 226, 0.829);
    text-transform: uppercase;
    letter-spacing: .2rem;
}

.subhead {
    letter-spacing: .1rem;
    text-transform: none;
    color: rgba(194, 239, 241, 0.829);
}

.subType {
    text-transform: capitalize;
}

.ratings {
    margin: 1rem;
    text-align: center;
}

.tags-container {
    margin: 2rem;
}

.tags-container>span {
    border: .15rem solid black;
    border-radius: .5rem;
    margin: .5rem;
    padding: .5rem;
    background: linear-gradient(to left, rgba(235, 205, 33, 0.288), rgba(77, 245, 209, 0.027));
}

.image-section {
    margin-top: 2rem;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.image {
    -webkit-animation: roll 2.5s ease forwards;
            animation: roll 2.5s ease forwards;
}

@-webkit-keyframes roll {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes roll {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

.details-section {
    text-align: right;
    margin-top: 2rem;
}

.progressBar {
    -webkit-animation: linearProgress 3s forwards;
            animation: linearProgress 3s forwards;
}

@-webkit-keyframes linearProgress {
    from {
        width: 0;
    }
}

@keyframes linearProgress {
    from {
        width: 0;
    }
}

@media screen and (max-width: 1000px) {
    .movie-Board {
        display: flex;
        flex: 1 1;
        flex-direction: column;
    }

    .details-section {
        text-align: center;
    }

    .tags-container {
        margin: 0;
        display: flex;
        flex-direction: row;
        flex: 1 1;
        flex-wrap: wrap;
        justify-content: center;
    }

    .tags-container>span {
        border: .15rem solid black;
        border-radius: .5rem;
        margin: .5rem;
        padding: .5rem;
        background: linear-gradient(to left, rgba(235, 205, 33, 0.288), rgba(77, 245, 209, 0.027));
    }
}
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: rgba(0, 0, 0, 0.89);
  color: white;
  min-height: 100vh;
  font-family: Poppins, Helvetica, sans-serif;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

nav {
  padding-bottom: 1rem;
}

.navStyle {
  right: 0;
  position: absolute;
  font-size: 1.4rem;
  letter-spacing: .5rem;
  cursor: pointer;
}

.link {
  color: yellow;
  text-decoration: none;
}

.link>li:hover {
  color: rgba(255, 255, 0, 0.534);
}

.errorPage {
  min-height: 90vh;
  z-index: -1;
  background: url(/MovieSearch/static/media/404.6695af99.svg);
  background-size: 100% 100vh;
  background-repeat: no-repeat;
  color: white;
  -webkit-animation: scaleUp 1.6s forwards;
          animation: scaleUp 1.6s forwards;
}

@-webkit-keyframes scaleUp {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

@keyframes scaleUp {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

.errorPage>h4 {
  float: right;
  margin: 1rem;
  color: rgba(255, 255, 0, 0.548);
}

h4>.icon_arrow {
  font-weight: 900;
  font-size: 2rem;
}

.para {
  position: relative;
  -webkit-text-stroke-color: rgb(247, 38, 10);
  -webkit-text-stroke-width: 1px;
  transition: all .4s;
  font-size: 7rem;
  display: inline;
}

.para::before {
  content: '404';
  position: absolute;
  top: 0;
  left: 0;
  color: #ff4800;
  z-index: -1;
  transition: all 0.4s;
}

.para:hover::before {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.para:hover {
  color: #ffffff;
}

footer {
  position: fixed;
  width: 100%;
  background-color: black;
  text-align: center;
  bottom: 0;
}

a {
  color: white;
  text-decoration: none;
}

footer>p {
  font-size: .6rem;
}

@media screen and (max-width: 900px) {
  nav {
    padding-bottom: 2rem;
  }

}
